.dashboard {
    display: flex;
    width: 100%;
  }
  
  .sidebar {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 200px;
    height: 100vh;
    background-color: white;
    padding: 20px;
  }
  
  .sidebar > div {
    margin-bottom: 80px;
  }
  
  .upload {
    position: relative;
    width: 100%;
    align-items: left;
  }
  
  .csv-input {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    cursor: pointer;
  }
  
  .upload-text {
    text-align: left;
  }
  
  .upload-area {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    flex-grow: 1; /* Take up remaining vertical space */
    background-color: #ddd;
    padding: 20px;
  }
  
  .upload-button {
    width: 200px;
    height: 50px;
    background-color: #4CAF50;
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 20px;
    cursor: pointer;
  }
  
  .upload-num {
    width: 200px;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 20px;
    font-size: 2rem;
    font-weight: bold;
  }
 
  .space-bar{
    display: flex;
    flex-direction: column;
    align-items: left;
    width: 200px;
    height: 100vh; 
   
  }

  .a1{
    padding: 6%;
  }
  .centered-text1 {
    font-size: 30px; 
    color: black; 
    align-content: right;
  }
  .circle-image {
    position: absolute;
    top: 0; 
    left: 0; 
    width: 50px; 
    height: 20px; 
  }