.container {
  display: flex;
  height: 100vh;
}

.blue-part {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  flex: 1;
  background-color: #605BFF;
  ;
}

.circle-image {
  position: absolute;
  top: 0; 
  left: 0; 
  width: 50px; 
  height: 50px; 
}

.logo-image {
  position: absolute;
  bottom: 20px; /* Adjust as needed */
}
.right-part {
  flex: 1;
  padding: 2rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #f5f5f5;
}

.right-part h2 {
  margin-bottom: 1rem;
}

.right-part label,
.right-part input,
.right-part button {
  width: 100%;
  margin-bottom: 0.5rem;
}

.right-part input[type="email"],
.right-part input[type="password"] {
  padding: 0.5rem;
}

.right-part button {
  padding: 0.5rem;
  background-color: #4CAF50;
  color: white;
  border: none;
  cursor: pointer;
}

.right-part button:hover {
  background-color: #45a049;
}

.button-container {
  display: flex;
}

.button-container button {
  margin-right: 10px;
  padding: 10px;
  font-size: 16px;
}
.forgot-password {
  font-size: 14px;
  color: #555;
 
}

.centered-text {
  font-size: 48px; 
  color: white; 
}